<template>
  <div>
    <Navbar />
    <Introduction @orderNow="display_order_now = true" />
    <Menu @openMenu="openMenu" />
    <AboutUs />
    <Featured @openFeatured="openFeatured" />
    <Contacts />

    <!-- DIALOGS -->
    <OrderNow v-if="display_order_now" @closeOrderNow="display_order_now = false" />
    <ViewMenu v-if="display_view_menu" :menu="menu" @closeMenu="display_view_menu = false" />
    <ViewFeatured v-if="display_featured" :image_no="featured" @order="display_order_now = true" @closeFeatured="display_featured = false" />
  </div>
</template>

<script>
import Navbar from '../components/Navbar.vue'
import Introduction from '../components/Introduction.vue'
import Menu from '../components/Menu.vue'
import AboutUs from '../components/AboutUs.vue'
import Featured from '../components/Featured.vue'
import Contacts from '../components/Contacts.vue'
import OrderNow from '../components/OrderNow.vue'
import ViewMenu from '../components/ViewMenu.vue'
import ViewFeatured from '../components/ViewFeatured.vue'

export default {
  components: {
    Navbar,
    Introduction,
    Menu,
    AboutUs,
    Featured,
    Contacts,
    OrderNow,
    ViewMenu,
    ViewFeatured
  },

  data() {
    return {
      display_order_now: false,
      display_view_menu: false,
      display_featured: false,
      menu: null,
      featured: null
    }
  },

  methods: {
    openMenu(data) {
      this.menu = data
      this.display_view_menu = true
    },

    openFeatured(data) {
      this.featured = data
      this.display_featured = true
    }
  }
}
</script>

<style>
.anchor {
  display: block;
  position: relative;
  top: -70px;
  visibility: hidden;
}
</style>
